import { Component, OnInit, Input, TemplateRef, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { UploadFile, UploadXHRArgs, UploadFilter } from 'ng-zorro-antd';
import { Observable, Subscription } from 'rxjs';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { uuidv1 } from '@pixelmon/util';
import { UploadServiceToken } from './upload-interface';

declare const baidubce: any;

@Component({
  selector: 'p-upload',
  exportAs: 'pUpload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.less'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: UploadComponent,
      multi: true,
    },
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class UploadComponent implements OnInit, ControlValueAccessor {
  @Input() accept = 'image/png,image/jpeg,image/gif,image/bmp'; // æ¥åä¸ä¼ çæä»¶ç±»å, è¯¦è§https://developer.mozilla.org/en-US/docs/Web/HTML/Element/input/file#accept
  @Input() action = ''; // ä¸ä¼ çå°å
  @Input() directory = false; // æ¯å¦æ¯æä¸ä¼ æä»¶å¤¹
  @Input() disabled = false; // æ¯å¦ç¦ç¨
  @Input() limit = 9; // éå¶åæ¬¡æå¤ä¸ä¼ æ°éï¼nzMultiple æå¼æ¶ææï¼0 è¡¨ç¤ºä¸é
  @Input() size = 0; // éå¶æä»¶å¤§å°ï¼åä½ï¼KBï¼0 è¡¨ç¤ºä¸é
  @Input() fileType = 'image/png,image/jpeg,image/gif,image/bmp'; // éå¶æä»¶ç±»åï¼ä¾å¦ï¼image/png,image/jpeg,image/gif,image/bmp
  @Input() listType: 'text' | 'picture' | 'picture-card' = 'picture-card'; // ä¸ä¼ åè¡¨çåå»ºæ ·å¼ï¼æ¯æä¸ç§åºæ¬æ ·å¼
  @Input() multiple = true; // æ¯å¦æ¯æå¤éæä»¶ï¼ie10+ æ¯æ
  @Input() showButton = true; // æ¯å¦å±ç¤ºä¸ä¼ æé®
  @Input() placeholder = 'ä¸ä¼ '; // å ä½æç¤ºè¯­
  @Input() customContent: TemplateRef<any>; // èªå®ä¹content
  @Input() maxLength = Infinity; // æå¤ä¸ä¼ æ°

  @Input() bucket = 'bucket'; // ç¾åº¦BOSä¸çå½åç©ºé´
  @Input() fastUpload = true; // æ¯å¦ä½¿ç¨å¿«ä¼ 

  @Input() filter: UploadFilter[] = [
    {
      name: 'maxLength',
      fn: fileList => {
        // éå¶æå¤§ä¸ä¼ æ°é
        return fileList.slice(0, this.maxLength - this.fileList.length);
      },
    },
  ];

  // æ¯å¦æ¾ç¤ºé¢è§å¾æ 
  @Input() set showPreviewIcon(value: boolean) {
    this.showUploadList = { ...this.showUploadList, showPreviewIcon: value };
  }

  // æ¯å¦æ¾ç¤ºå é¤å¾æ 
  @Input() set showRemoveIcon(value: boolean) {
    this.showUploadList = { ...this.showUploadList, showPreviewIcon: value };
  }

  // æ¯å¦æ¾ç¤ºé¢è§å¾æ åå é¤å¾æ 
  showUploadList = {
    showPreviewIcon: true,
    showRemoveIcon: true,
    hidePreviewIconInNonImage: true, // ä¸æ¯å¾çæ¶éèé¢è§å¾æ 
  };

  private bosClient: any = null; // ç¾åº¦ä¸ä¼ å®¢æ·ç«¯
  private isSupportWorker = false; // æµè§å¨æ¯å¦æ¯æWorker

  isLoading = false; // ç¾åº¦ä¸ä¼ å®¢æ·ç«¯åå§åä¸­
  fileList: UploadFile[] = []; // æä»¶åè¡¨ï¼ç»å®ControlValueAccessor
  fileListChange: (files: UploadFile[]) => void; // æä»¶åè¡¨æ¹åï¼ç»å®ControlValueAccessor

  // é¢è§modalåæ°å¯¹è±¡
  previewModal = {
    visible: false, // æ¯å¦æ¾ç¤ºé¢è§modal
    image: '', // é¢è§å¾ç
  };

  // ä¸ä¼ æä»¶ä¹åçé©å­,è¥è¿å false ååæ­¢ä¸ä¼ ãæ³¨æï¼å¡å¿ä½¿ç¨ => å®ä¹å¤çæ¹æ³
  @Input() beforeUpload: (file: UploadFile, fileList: UploadFile[]) => boolean | Observable<boolean>;

  // ç¹å»ç§»é¤æä»¶æ¶çåè°ï¼è¿åå¼ä¸º false æ¶ä¸ç§»é¤ãæ³¨æï¼å¡å¿ä½¿ç¨ => å®ä¹å¤çæ¹æ³ã
  @Input() remove: (file: UploadFile) => boolean | Observable<boolean>;

  // ç¹å»æä»¶é¾æ¥æé¢è§å¾æ æ¶çåè°ï¼æ³¨æï¼å¡å¿ä½¿ç¨ => å®ä¹å¤çæ¹æ³
  @Input() preview: (file: UploadFile) => void = (file: UploadFile) => {
    this.previewModal.image = file.url! || file.thumbUrl!;
    this.previewModal.visible = true;
    this._cdr.detectChanges();
  };

  // éè¿è¦çé»è®¤çä¸ä¼ è¡ä¸ºï¼å¯ä»¥èªå®ä¹èªå·±çä¸ä¼ å®ç°ï¼æ³¨æï¼å¡å¿ä½¿ç¨ => å®ä¹å¤çæ¹æ³
  @Input() customRequest: (subject: UploadXHRArgs) => Subscription = uploadSubject => {
    const uploadFile = uploadSubject.file;

    // uploadKeyç¨äºæ è¯æä»¶å¯ä¸æ§ï¼å¦æéå¤Putåä¸ä¸ªkeyçObjectï¼ä¹åä¸ä¼ çæ°æ®å°è¢«è¦ç
    this.getUploadKey(uploadFile).then(uploadKey => {
      // å°è¯ç§ä¼ 
      if (this.isSupportWorker && this.fastUpload) {
        // åç¨HEADè¯·æ±æ ¹æ®keyå¤æ­æ¯å¦å·²ä¸ä¼ è¿è¯¥æä»¶
        this.bosClient
          .getObjectMetadata(this.bucket, uploadKey)
          .then(event => {
            // ä¸ä¼ è¿äºï¼ç´æ¥åæ¾
            uploadFile.url = `${this.bosClient.config.endpoint}/v1/${this.bucket}/${uploadKey}`;
            uploadSubject.onSuccess!('ä¸ä¼ æå', uploadFile, event);
          })
          .catch(
            // æ²¡æä¸ä¼ è¿ï¼ä½¿ç¨ç¾åº¦bosç´ä¼ 
            () => this.bosUpload(uploadSubject, this.bucket, uploadKey, uploadFile),
          );
      } else {
        // ç¾åº¦bosç´ä¼ 
        this.bosUpload(uploadSubject, this.bucket, uploadKey, uploadFile);
      }
    });

    // ZORROè¦æ±è¿åä¸ä¸ªSubscription
    return new Subscription();
  };

  constructor(private _cdr: ChangeDetectorRef, private _uploadSrv: UploadServiceToken) {
    this.initBosClient();
  }

  ngOnInit() {
    // å¤æ­æ¯å¦æ¯æWorker
    if (typeof Worker !== 'undefined') {
      this.isSupportWorker = true;
    } else {
      if (this.fastUpload) {
        console.error('æµè§å¨ä¸æ¯æWorkerï¼æ æ³ä½¿ç¨å¿«ä¼ ï¼');
      }
    }
  }

  writeValue(value) {
    this.fileList = value || [];
    this.fileList.forEach(file => {
      file.uid = file.uid || uuidv1();
    });
    this._cdr.detectChanges(); // for issueï¼https://github.com/angular/angular/issues/10816
  }

  registerOnChange(fn: any) {
    this.fileListChange = fn;
  }

  registerOnTouched() {}

  // åå§åç¾åº¦ä¸ä¼ å®¢æ·ç«¯
  initBosClient() {
    if (this._uploadSrv.getConfig()) {
      this.isLoading = true;
      this._uploadSrv.getConfig()!.subscribe(config => {
        this.bosClient = new baidubce.sdk.BosClient({
          endpoint: config.endpoint,
          credentials: {
            ak: config.ak,
            sk: config.sk,
          },
          sessionToken: config.sessionToken,
        });
        this.isLoading = false;
        this._cdr.detectChanges();
      });
    }
  }

  getUploadKey(file: UploadFile): Promise<string> {
    return new Promise(resolve => {
      // ä½¿ç¨Worker
      if (this.isSupportWorker && this.fastUpload) {
        const worker = new Worker(this._uploadSrv.workerUrl);

        worker.onerror = error => {
          console.error('Workerå¼å¸¸ï¼å·²å³é­ï¼', error);
          worker.terminate();
        };

        worker.postMessage(file);

        worker.onmessage = event => {
          // ä½¿ç¨æä»¶md5+æåä¿®æ¹æ¶é´+æä»¶å¤§å°+æä»¶åæ¥ä¿è¯å¯ä¸æ§ï¼åæ¶ç¸åçæä»¶çkeyä¹ä¼ç¸å,ç¨äºç§ä¼ 
          const key = `${event.data}${file.lastModified}${file.size}${file.name}`;
          resolve(key);
          worker.terminate();
        };
      } else {
        // ä¸ä½¿ç¨Worker
        // ä½¿ç¨å½åæ¶é´+åä½éæºç +æåä¿®æ¹æ¶é´+æä»¶å¤§å°+æä»¶åæ¥ä¿è¯å¯ä¸æ§ï¼æ­¤æ¶å ä¸ºç¸åæä»¶keyä¹ä¼ä¸åï¼æå®ç°ä¸äºç§ä¼ 
        const random = `${new Date().getTime()}${(Math.random() * 10000).toFixed(0)}`;
        const key = `${random}${file.lastModified}${file.size}${file.name}`;
        resolve(key);
      }
    });
  }

  // ä½¿ç¨ç¾åº¦bosç´ä¼ 
  bosUpload(uploadSubject: UploadXHRArgs, bucket: string, uploadKey: string, uploadFile: UploadFile) {
    this.bosClient
      .putObjectFromBlob(bucket, uploadKey, uploadFile)
      .then((event: HttpResponse<{}>) => {
        uploadFile.url = `${this.bosClient.config.endpoint}/v1/${bucket}/${uploadKey}`;
        uploadSubject.onSuccess!('ä¸ä¼ æå', uploadFile, event);
      })
      .catch((event: HttpErrorResponse) => {
        uploadSubject.onError!(event, uploadFile);
      });
  }

  onFileListChange() {
    // å»é¤ç¼©ç¥å¾ï¼å ä¸ºç¼©ç¥å¾å¤ªå¤§,è¦æ¯å¤é¨ä¸å¤çä¼å½±åHttpè¯·æ±éåº¦
    this.fileListChange(this.fileList.map(file => ({ ...file, thumbUrl: '' })));
    this._cdr.detectChanges();
  }

  onChange() {
    // èµå¼url
    this.fileList.forEach(file => {
      file.url = file.url || (file.originFileObj && (file.originFileObj as any).url);
    });
    this.onFileListChange();
  }
}
